@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&family=Raleway:wght@700&family=Roboto:wght@400;500;700;900&display=swap');

*{
  margin: 0;
  padding: 0;
}
body {
  background-color: 0;
  color: #222;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  
}

.wrapper {
 width:1280px;
 margin: 50px auto;
}

header{
position: relative;}

header .logo {
  font-weight: 600;
  font-size: 20xp;
}

header .presentation {
  margin: 50px 0;
  background: url('./img/bg.jpg') no-repeat;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center center;
  background-blend-mode:multiply;
  background-color: #bcbcbc;
  position: relative;
}

header .presentation::after{
  content: 'Quality goods for your house';
  position: absolute;
  top:100px;
  left:50px;
  width: 250px;
  font-size: 40px;
  font-weight: 600;
  color: #fff;
}

header .presentation::before {
  content: 'Many discounts';
  position: absolute;
  top: 250px;
  left: 50px;
  width: 300px;
  font-size: 15px;
  font-weight: 300;
  color: #fff;
}

header .shop-cart-button{
  float: right;
  cursor: pointer;
  transition: color,transform 500ms ease;
}

header .shop-cart-button:hover,
header .shop-cart-button.active{
  color:#dc3d3d;
  transform: scale(1.5);
}
header .shop-cart{
  position: absolute;
  top:50px;
  right: 0;
  width: 450px;
  background-color: #f5f5f5;
  box-shadow: 10px 1px 5px -1px rgba(115, 115, 115, 0.16);
  z-index: 100;
  padding: 20px;
  padding-bottom: 0;

}

header .shop-cart .item{
  width:100%;
  float: left ;
  margin-bottom: 20px;

}

header .shop-cart .item img{
  width: 70px;
  float: left;
  margin-right: 20px;
}



header .shop-cart .item h2{
font-size: 20px;
margin-bottom: 10px;
}

header .shop-cart .item p {
  color:#797979;
  font-weight: 600;
}

header .shop-cart .item .delete-icon{
  color: #ca5252;
  float: right;
  position: relative;
  top:-10px;
  cursor: pointer;
  transition: color, transform 500ms ease;
}

header .shop-cart .item .delete-icon:hover{
  color: #fb3232;
  transform: scale(1.5);

}

header .shop-cart .empty h2 {
  font-size:20px;
  margin-bottom: 20px;
}

header .shop-cart .summa{
float: left;
width:100%;
font-weight: 600;
font-size: 20px;
margin-bottom: 20px;

}

footer{
  text-align: center;
  margin-top: 100px;
}

header ul.nav{
  float: right;
  list-style: none;
}

header ul.nav li{
  display: inline;
  margin-left: 25px;
  cursor: pointer;
  transition: opacity 500ms ease;
}

header ul.nav li:hover{
  opacity:0.5;
}

.categories div{
display: inline-block;
background-color: #f5f5f5;
border-radius: 50px;
padding: 10px 20px;
margin-bottom: 25px;
margin-right: 15px;
cursor: pointer;
border: 1px solid transparent;
transition: all 500ms ease;
}

.categories div:hover {
  border-color:#cbcbcb;
  transform: scale(1.1);
}

main{
  display: flex;
  width:100%;
  flex-wrap:wrap;
  justify-content: space-between;

}

main .item {
  width: 30%;
  margin-bottom: 50px;
  background-color: #f5f5f5;
  overflow: hidden;
  position: relative;
  padding-bottom: 20px;
  border-radius: 10px 10px 0 0;
}

main .add-to-cart, .full-item .add-to-cart{
  position: absolute;
  right: 20px;
  bottom: 20px;
  background: #ca5252;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  font-weight: 600;
  transition: transform 500ms ease;
}

main .add-to-cart:hover, .full-item .add-to-cart:hover{
  transform: scale(1.5) translateY(-5px);
}

main h2, main p, main b, .full-item h2, .full-item p, .full-item b{
  margin: 10px 20px;
  color:'#333'
}

 main b, .full-item b{
  color:#5fa36a
}

main .item img:hover, .full-item img:hover{
  transform: scale(1.05);
}

main .item img, .full-item img{
  transition: transform 500ms ease;
  width: 100%;
  cursor: pointer;
}


.full-item{
  position: fixed;
  top:0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.8);
  z-index:9999;
  overflow: auto;
}

.full-item>div{
  width:600px;
  position: relative;
  margin:10% auto;
  padding: 40px 30px;
  background: #fff;
  border-radius: 9px;
}
.container {
      max-width: 400px;
      margin: 0 auto;
      padding: 20px;
      border: 1px solid #ccc;
      border-radius: 4px;
    }

    .form-group {
      margin-bottom: 10px;
    }

    .form-group label {
      display: block;
      font-weight: bold;
      margin-bottom: 5px;
    }

    .form-group input {
      width: 100%;
      padding: 5px;
      border: 1px solid #ccc;
      border-radius: 4px;
    }

    .form-group input[type="submit"] {
      background-color: #4CAF50;
      color: white;
      cursor: pointer;
    }



        .container-profile {
          position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: rgba(0, 0, 0, 0.8);
            z-index: 9999;
            overflow: auto;
        }
        .container-profile>div{
          width: 300px;
            position: relative;
            margin: 10% auto;
            padding: 40px 30px;
            background: #fff;
            border-radius: 9px;

        }
    
        .form-group {
          margin-bottom: 10px;
          margin-top: 10px;
        }
    
        .form-group label {
          display: block;
          font-weight: bold;
          margin-bottom: 5px;
        }
    
        .form-group input {
          width: 100%;
          padding: 5px;
          border: 1px solid #ccc;
          border-radius: 4px;
        }
    
        .form-group input[type="submit"] {
         margin-top: 10px;
          background-color: #4CAF50;
          color: white;
          cursor: pointer;
        }

        .container-profile-close{
          position: absolute;
          cursor: pointer;
          top: 15px;
          right: 15px;
          transition: transform 500ms ease;
        }
        .container-profile-close:hover {
          color: black;
          transform: scale(1.1);
        }